* {
  font-family: douzone, "Microsoft YaHei", "PingFang SC", "MS PGothic", "Hiragino Kaku Gothic ProN", "굴림", gulim, "Apple SD Gothic Neo", sans-serif;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
legend,
input,
textarea,
img,
button,
select {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #fff;
  -webkit-text-size-adjust: none;
}

img,
fieldset {
  border: 0;
  vertical-align: top;
}

em,
address {
  font-style: normal;
}

ul,
ol,
li {
  list-style: none;
}

textarea {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background-color: transparent;
  border: 0;
  word-break: keep-all;
  word-wrap: break-word;
}

button {
  appearance: none;
  -webkit-appearance: none;
  background-color: transparent;
  border: 0;
}

.blind {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus,
button:active {
  outline: none;
}

.ovr_y {
  min-height: 100%;
  height: 100%;
  overflow: hidden;
  touch-action: none;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 46px;
  padding: 0 20px;
  background-color: #fff;
  box-sizing: border-box;
  z-index: 10;
}

.header__logo {
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 92px;
  height: 19px;
  background-position: 0 -247px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.header__logo a {
  display: block;
  height: 100%;
}

.header__hambuger {
  position: relative;
  width: 24px;
  height: 24px;
}

.header__hambuger::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 18px;
  height: 14px;
  background-position: -97px -248px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 20;
  transform: translateZ(0);
}

.nav__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 46px;
  padding: 0 20px;
}

.nav__close {
  position: relative;
  width: 24px;
  height: 24px;
}

.nav__close::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 16px;
  height: 16px;
  background-position: -134px -248px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav__tab ul {
  border-bottom: 1px solid #efefef;
}

.nav__tab ul::after {
  content: '';
  display: block;
  clear: both;
}

.nav__tab ul li {
  position: relative;
  float: left;
  width: 50%;
  text-align: center;
}

.nav__tab--text {
  display: block;
  width: 100%;
  height: 38px;
  padding: 10px 36px;
  font-size: 14px;
  color: #a5a5a5;
  letter-spacing: -0.7px;
  box-sizing: border-box;
}

.nav__tab .active .nav__tab--text {
  font-weight: bold;
  color: #000;
}

.nav__tab .active .nav__tab--text::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 50%;
  width: 120px;
  height: 2px;
  background-color: #000;
  transform: translateX(-50%);
}

.nav__tabButton {
  overflow-y: auto;
  position: absolute;
  top: 105px;
  left: 0;
  right: 0;
  bottom: 68px;
}

.nav__tabButton--text {
  display: block;
  position: relative;
  padding: 16px 20px 16px 56px;
  font-size: 15px;
  color: #000;
  line-height: 18px;
  letter-spacing: -0.23px;
  box-sizing: border-box;
}

.nav__tabButton ul li.active {
  background-color: rgba(0, 0, 0, 0.06);
}

.nav__tabButton .nav--icon {
  position: absolute;
  top: 50%;
  left: 20px;
  width: 24px;
  height: 24px;
  transform: translateY(-50%);
}

.nav__tabButton .nav--icon::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 18px;
  height: 19px;
  background-position: 0 -271px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav__tabButton--item1 .nav--icon::before {
  margin: -10px 0 0 -9px;
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 18px;
  height: 19px;
  background-position: 0 -271px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav__tabButton--item2 .nav--icon::before {
  margin: -9px 0 0 -9px;
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 18px;
  height: 18px;
  background-position: -23px -271px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav__tabButton--item3 .nav--icon::before {
  margin: -8px 0 0 -10px;
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 22px;
  height: 15px;
  background-position: -46px -271px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav__tabButton--item4 .nav--icon::before {
  margin: -9px 0 0 -9px;
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 18px;
  height: 18px;
  background-position: -73px -271px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav__tabButton--item5 .nav--icon::before {
  margin: -11px 0 0 -10px;
  background-image: url("/src/landing/imgs/sp_nahago_mobile.png");
  width: 19px;
  height: 19px;
  background-position: -96px -271px;
  background-size: 475px 475px;
  -webkit-background-size: 475px 475px;
}

.nav__bottom {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
}

.nav__info {
  display: flex;
  height: 48px;
  padding: 0 20px;
  align-items: center;
}

.nav__info--text {
  font-size: 13px;
  color: #a5a5a5;
  letter-spacing: -0.65px;
}

.nav__info--dash {
  display: inline-block;
