}

.content__spot--text {
  margin-top: 10px;
  font-size: 12px;
  color: #404040;
  letter-spacing: -0.78px;
  line-height: 19px;
}

.content__button {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 13px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.content__button button {
  width: 100%;
  height: 36px;
  background-color: transparent;
  border: 0;
  border-radius: 10px;
  font-size: 13px;
  font-weight: bold;
  color: #fff;
  letter-spacing: -0.65px;
  appearance: none;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
