.content__detail {
  display: inline-block;
  margin-top: 20px;
  padding: 7px 20px 7px;
  border: 1px solid #4a4a4a;
  border-radius: 17px;
  font-size: 12px;
  font-weight: bold;
  color: #4a4a4a;
}

.content__free {
  padding: 50px 0 0;
  background-color: #f7f8fc;
  text-align: center;
}

.content__free--title {
  font-size: 18px;
  letter-spacing: -0.9px;
}

.content__free--title em {
  font-family: douzonetitle;
  font-size: 22px;
  color: #1c90fb;
  letter-spacing: -0.22px;
}

.content__free--title.number {
  font-size: 18px;
  letter-spacing: -0.36px;
}

.content__free--text {
  margin-top: 12px;
  font-size: 12px;
  color: #4a4a4a;
  line-height: 17px;
  letter-spacing: -0.12px;
}

.content__thumb {
  position: relative;
  width: 100%;
  height: 276px;
  background: url(https://static.wehago.com/html/landing/img/nahago_mobile/img_free.png) no-repeat 50% 0/370px 276px;
}

.content__thumb--link {
  position: absolute;
  bottom: 40px;
  left: 50%;
  width: 220px;
  height: 50px;
  margin-left: -113px;
}

.content__introduce {
  overflow: hidden;
  position: relative;
  height: 430px;
  padding-top: 50px;
  background-image: linear-gradient(to bottom, #002798 20%, #2a4dc5 50%, #5c51d8);
  text-align: center;
}

.content__introduce--title {
  position: relative;
  font-size: 18px;
  color: #fff;
  line-height: 24px;
  letter-spacing: -0.54px;
