}

.content__introduce .pattern {
  position: absolute;
  top: -235px;
  left: 50%;
  width: 507px;
  height: 507px;
  margin-left: -360px;
  background: url(https://static.wehago.com/html/landing/img/nahago_mobile/bg_introduce_pattern.png) no-repeat 0 0/507px 507px;
}

.content__introduce::after {
  content: '';
  position: absolute;
  bottom: -102px;
  width: 333px;
  height: 371px;
  margin-left: -153px;
  background: url(https://static.wehago.com/html/landing/img/nahago_mobile/bg_introduce_phone.png) no-repeat 50% 0/333px 371px;
}

.content__information {
  max-width: 300px;
  margin: 14px auto 0;
  text-align: left;
}

.content__information li {
  position: relative;
  padding-left: 6px;
  font-size: 9px;
  color: #fff;
  line-height: 16px;
  letter-spacing: -0.45px;
}

.content__information li::before {
  content: '';
  position: absolute;
  top: 6px;
  left: 0;
  width: 2px;
  height: 2px;
  background-color: #c4c4c4;
}

.content__swipe {
  padding: 50px 0;
  background-color: #f7f8fc;
}

.content__box--title {
  font-size: 18px;
  font-weight: normal;
  color: #4a4a4a;
  letter-spacing: -0.9px;
  text-align: center;
}

.content__box--text {
  margin-top: 3px;
  text-align: center;
  line-height: 23px;
}

.content__box--text em {
  font-family: douzonetitle;
  font-weight: bold;
  font-size: 26px;
  color: #0424b7;
  letter-spacing: -0.52px;
}

.content__box--subtext {
  display: inline-block;
  font-family: douzonetitle;
  font-weight: bold;
  font-size: 22px;
  color: #000;
  letter-spacing: -1.1px;
  vertical-align: top;
}

.content__box--infoText {
  margin-top: 10px;
  font-size: 12px;
  color: #000;
  line-height: 17px;
  letter-spacing: -0.6px;
  text-align: center;
}

.content__movie {
  height: 56.25%;
  background-color: rgba(0, 0, 0, 0.1);
}

.content__swiperBox {
  overflow: hidden;
  position: relative;
  margin-top: 30px;
  padding: 0 29px 23px;
}

.content__swiperBox .swiper .swiper-slide.item1 {
  width: 230px;
  height: 220px;
}

.content__swiperBox .swiper .swiper-slide.item2 {
  width: 243px;
  height: 220px;
}

.content__swiperBox .swiper .swiper-slide.item3 {
  width: 239px;
  height: 220px;
}

.content__swiperBox .swiper .swiper-pagination {
  bottom: 0;
}

.content__swiperBox .swiper .swiper-pagination .swiper-pagination-bullet {
  width: 5px;
  height: 5px;
  background-color: #d9d9d9;
}

.content__swiperBox .swiper .swiper-pagination .swiper-pagination-bullet-active {
  width: 12px;
  height: 5px;
  border-radius: 3px;
  background-color: #939393;
}

@media (min-width: 768px) {
  .content__swiperBox .swiper .swiper-pagination {
    display: none;
  }
  .content__swiperBox .swiper .swiper-wrapper {
    justify-content: center;
  }
  .content__swiperBox .swiper .swiper-wrapper .swiper-slide + .swiper-slide {
    margin-left: 18px;
  }
}

.content__img {
  position: relative;
}

.content__img img {
  width: 100%;
  height: 100%;
}

.content__img [class*="item__link"] {
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: auto;
  height: 29px;
  padding: 0 8px;
  border-radius: 4px;
  background-color: transparent;
