}

.footer {
  padding: 24px 0;
  background-color: #fff;
  border-top: 1px solid #e6e6e6;
}

.footer__link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__link li {
  position: relative;
  margin-left: 7px;
}

.footer__link li::before {
  content: '';
  display: inline-block;
  width: 1px;
  height: 11px;
  margin-top: 6px;
  background: #5f5f5f;
  vertical-align: top;
}

.footer__link li:first-child::before {
  content: none;
}

.footer__link--text {
  font-size: 12px;
  color: #444;
  letter-spacing: -0.6px;
}

.footer__copyright {
  margin-top: 14px;
  font-size: 11px;
  color: #7b7b7b;
  line-height: 18px;
  text-align: center;
}
